<template>
  <div style="margin: 30px 0 0 0">
    <div class="gonggao bdb_d fix">
      <div class="txtScroll-top l fix zy_cur">
        <div class="l tit">
          <b class="dib vm">当前位置：</b>
        </div>
        <p class="l">
          <router-link to="/index">首页</router-link><span>成果展示</span>
        </p>
      </div>
    </div>

    <div class="zy_contentbox">
      <div class="zy_content fix">
        <div class="zy_left_con">
          <div class="zy_twonav">
            <h2 class="tit">成果展示</h2>
            <ul>
              <li
                v-for="(item, index) in menu"
                :class="{ cur: currpage == item }"
              >
                <a href="javascript:void(0)" @click="loadData(item)">{{
                  item
                }}</a>
              </li>
            </ul>
          </div>
        </div>

        <div class="zy_right_con">
          <div v-if="currpage == '成果展示'">
            <div class="zy_content fix">
              <div class="zy_conbox">
                <div class="zy_newsxq">
                  <div class="title">
                    <span>{{ title }}</span>
                  </div>
                  <div class="main">
                    <div v-html="mainText"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      currpage: "成果展示",
      menu: ["成果展示"],
      article: {},
      list: [],
      page: {},
      title: "",
      mainText: "",
    };
  },
  created() {
    this.loadData("成果展示");
    var that = this;
    axios
      .post("/api/index/intro/get", { id: 5 })
      .then((d) => {
        console.log(d);
        if (d.data.code == 200) {
          that.title = d.data.data.title;
          that.mainText = d.data.data.content;
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    loadData: function (page) {
      this.currpage = page;
    },
  },
};
</script>

<style scoped>
.zy_newsxq {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  justify-content: center;
}
.title {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  text-align: center;
  margin: 20px 0 20px 0;
  padding: 0 0 20px 0;
  border-bottom: 1px solid #ddd;
}
.main {
  width: 100%;
}
</style>



